import { EventDispatcher } from "./events/eventdispatcher"


export class JsonLoader extends EventDispatcher{

    public static LOADED:string = "loaded";

    public constructor(){
        super();
        $.ajax({
            url:'includes/dataset.json',
            success:data=>this.onJSONLoaded(data,this)
        });
    }

    private onJSONLoaded(e,ref){
        ref.dispatchEvent(JsonLoader.LOADED,e);
    }
}