export class Model
{
    private static _instance: Model;
    
    private nScale:number;
    private nImageWidth:number;
    private nImageHeight:number;
    private refController:any;
    private nOriginalWidth:number = 984;
    private nOriginalHeight:number = 482;
    private ifDropped:string;
    
    private constructor()
    {
        //...
    }

    public static get Instance()
    {
        // Do you need arguments? Make it a regular method instead.
        return this._instance || (this._instance = new this());
    }

    public get scale():number{
        return this.nScale;
    }

    public set scale(num){

        this.nScale = num;
        
    }

    public get controllerRef():any{
        return this.refController;
    }

    public set controllerRef(ref){

        this.refController = ref;
        
    }

    public get imageWidth():number{
        return this.nImageWidth;
    }

    public set imageWidth(width){

        this.nImageWidth = width;
        
    }

    public get imageHeight():number{
        return this.nImageHeight;
    }

    public set imageHeight(height){

        this.nImageHeight = height;
        
    }

    public get originaleWidth():number{
        return this.nOriginalWidth;
    }

    public get originalHeight():number{
        return this.nOriginalHeight;
    }

}